import React from 'react';
import PropTypes from 'prop-types';

import 'react-dropzone-uploader/dist/styles.css';
import { getDroppedOrSelectedFiles } from 'html5-file-selector';
import Dropzone from 'react-dropzone-uploader';

import { lighten, withStyles } from '@material-ui/core/styles';
import {
	Container,
	Paper,
	Grid,
	LinearProgress,
	List,
	ListItem,
	ListItemAvatar,
	Avatar,
	ListItemText,
	ListItemSecondaryAction,
	IconButton
} from '@material-ui/core';

import FolderIcon from '@material-ui/icons/Folder';

import Markdown from 'markdown-to-jsx';
import Upload_File from '../../assets/markdown/Upload_File.md';

function findOccurances(of, inn) {
	var array = [];
	for (var obj in inn) {
		if (inn[obj] === of) {
			array.push(obj);
		}
	}
	return array;
}

const Input = ({ accept, onFiles, files, getFilesFromEvent }) => {
	const text = files.length > 0 ? 'Add more files' : 'Click to Choose Files / Drog whole folder';

	return (
		<Container style={{ padding: '3em' }}>
			<label
				style={{
					backgroundColor: '#007bff',
					color: '#fff',
					cursor: 'pointer',
					padding: 15,
					borderRadius: 3,
					fontWeight: 'bold'
				}}
			>
				{text}
				<input
					style={{ display: 'none' }}
					type="file"
					accept={accept}
					multiple
					onChange={(e) => {
						getFilesFromEvent(e).then((chosenFiles) => {
							onFiles(chosenFiles);
						});
					}}
				/>
			</label>
		</Container>
	);
};

const BorderLinearProgress = withStyles({
	root: {
		height: 10,
		backgroundColor: lighten('#bdbdbd', 0.5)
	},
	bar: {
		borderRadius: 20,
		backgroundColor: '#ff6c5c'
	}
})(LinearProgress);

const BorderLinearProgressSuccess = withStyles({
	root: {
		height: 10,
		backgroundColor: lighten('#81c784', 0.5)
	},
	bar: {
		borderRadius: 20,
		backgroundColor: '#81c784'
	}
})(LinearProgress);

const Preview = ({ meta }) => {
	const { name, percent, status } = meta;
	return (
		<Grid container direction="row" justify="center" alignItems="center" style={{ padding: '1.2em' }}>
			<Grid item xs={4} style={{ textAlign: 'left', fontWeight: 'bold' }}>
				{name}
			</Grid>
			<Grid item xs={5} style={{ textAlign: 'center' }}>
				{(status === 'done') & (percent === 100) ? (
					<BorderLinearProgressSuccess variant="determinate" value={percent} />
				) : (
						<BorderLinearProgress color="secondary" variant="determinate" value={percent} />
					)}
			</Grid>
			<Grid item xs={2} style={{ textAlign: 'right', color: 'grey' }}>
				{(status === 'done') & (percent === 100) ? (
					<div>Success</div>
				) : (status === 'uploading') & (percent === 100) ? (
					<div>Receiving</div>
				) : (
							<div>Uploading</div>
						)}
			</Grid>
		</Grid>
	);
};

class UploadFile extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			files: [],
			md: '',
			stamp: ''
		};
	}

	componentWillMount() {
		fetch(Upload_File).then((res) => res.text()).then((md) => {
			this.setState({ md });
		});
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.stamp !== this.props.stamp) {
			this.setState({
				stamp: nextProps.stamp
			});
		}
	}

	getUploadParams = ({ file, meta }) => {
		const body = new FormData();
		body.append(this.state.stamp, file);
		return { url: process.env.REACT_APP_API_ENTRY + '/upload', body };
	};

	getFilesFromEvent = (e) => {
		return new Promise((resolve) => {
			getDroppedOrSelectedFiles(e).then((chosenFiles) => {
				resolve(chosenFiles.map((f) => f.fileObject));
			});
		});
	};

	handleChangeStatus = ({ meta, file }, status) => {
		var tmpFile = this.state.files;
		tmpFile[file.name] = status;

		console.log(tmpFile);

		this.setState({ files: tmpFile });
		// console.log(status, meta, file);
		// if(this.state.files[file.name] === undefined){
		// 	this.state.files[file.name] = status
		// } else {
		// 	this.state.files[file.name] = status
		// }
		// console.log(status)
		// console.log(meta)
		// console.log(file)
	};

	handleSubmit = (files, allFiles) => {
		console.log(files.map((f) => f.meta));
		this.props.onUploadDone();
		// allFiles.forEach((f) => f.remove());
	};

	render() {
		const { classes } = this.props;

		return (
			<Grid container spacing={3}>
				<Grid item xs={12}>
					<Markdown className={classes.markdown} children={this.state.md} />
				</Grid>
				<Grid item xs={12}>
					<Dropzone
						submitButtonContent={'Finish Uploading'}
						canCancel={false}
						multiple={true}
						getUploadParams={this.getUploadParams}
						onChangeStatus={this.handleChangeStatus}
						onSubmit={this.handleSubmit}
						InputComponent={Input}
						PreviewComponent={Preview}
						getFilesFromEvent={this.getFilesFromEvent}
						styles={{ dropzone: { maxHeight: 600 } }}
						disabled={(files) =>
							files.some((f) => ['reparing', 'getting_upload_params', 'uploading'].includes(f.meta.status))}
						accept=".idat"
					/>
				</Grid>
				<Container style={{ margin: '1em' }}>
					<Grid container spacing={2}>
						{['Preparing', 'Uploading', 'Done', 'Error'].map((status, index) => {
							return (
								<Grid item xs={6} md={3} key={index}>
									<Paper>
										<List>
											<ListItem>
												<ListItemAvatar>
													<Avatar>
														<FolderIcon />
													</Avatar>
												</ListItemAvatar>
												<ListItemText primary={status} />
												<ListItemSecondaryAction>
													<IconButton edge="end" aria-label="delete">
														{/* {this.state.files.filter(x => x===status.toLowerCase().length)} */}
														{findOccurances(status.toLowerCase(), this.state.files).length}
													</IconButton>
												</ListItemSecondaryAction>
											</ListItem>
										</List>
									</Paper>
								</Grid>
							);
						})}
					</Grid>
				</Container>
			</Grid>
		);
	}
}

UploadFile.propTypes = {
	classes: PropTypes.object.isRequired
};

const styles = (theme) => ({
	markdown: {
		...theme.typography.body2,
		fontSize: 14
	}
});

export default withStyles(styles)(UploadFile);
