import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { withStyles } from '@material-ui/core/styles';
import { CssBaseline, Container, Typography, Button, Toolbar, IconButton, Avatar } from '@material-ui/core';

import SearchIcon from '@material-ui/icons/Search';

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sections: [
        {
          name: 'Home',
          url: '/'
        },
        {
          name: 'Upload',
          url: '/upload'
        },
        {
          name: 'Result',
          url: '/result'
        }
      ]
    }
  }

  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth="lg">
          <Toolbar className={classes.toolbar}>
            <Avatar alt="logo" src={require('../assets/image/EpiClockR_Logo.png')} className={classes.avatar} />
            <Typography
              component="h2"
              variant="h5"
              color="inherit"
              align="left"
              noWrap
              className={classes.toolbarTitle}
            >
              EpiClockR
          </Typography>
            {/* <IconButton color="inherit">
              <SearchIcon />
            </IconButton>
            <Button variant="outlined" color="inherit" size="small">
              Sign in/up
          </Button> */}
          </Toolbar>
          <Toolbar component="nav" variant="dense" className={classes.toolbarSecondary}>
            {this.state.sections.map((section, index) => (
              // <Link href='/about' as='about.html' key={index}>

              <Link
                color="black"
                key={index}
                variant="body2"
                className={classes.toolbarLink}
                to={section.url}
              >
                {section.name}
              </Link>

            ))}
          </Toolbar>
        </Container>
      </React.Fragment>
    );
  }
}

Header.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  toolbar: {
    borderBottom: `1px solid grey`,
    color: 'white'
  },
  toolbarTitle: {
    flex: 1,
    fontWeight: 'bolder'
  },
  toolbarSecondary: {
    // justifyContent: 'space-between',
    overflowX: 'auto',
  },
  toolbarLink: {
    padding: theme.spacing(1),
    flexShrink: 0,
    color: 'white',
    fontWeight: 'bolder',
    textDecoration: 'none'
  },
  imageIcon: {
    height: '1.2em',
    width: '1.2em'
  },
  avatar: {
    marginRight: '0.5em',
    width: '3em',
    height: '1.5em',
  },
});

export default withStyles(styles)(Header);
