import React from 'react';
import PropTypes from 'prop-types';

import { withStyles, makeStyles } from '@material-ui/core/styles';
import SwipeableViews from 'react-swipeable-views';
import clsx from 'clsx';
import { CssBaseline, Container, Paper, Typography, Button, Box, Grid, Stepper, Step, StepConnector, StepLabel } from '@material-ui/core';

import EmailIcon from '@material-ui/icons/Email';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import PlaylistAddCheckIcon from '@material-ui/icons/PlaylistAddCheck';

import Email from '../components/Upload/Email'
import UploadFile from '../components/Upload/UploadFile'
import CheckFile from '../components/Upload/CheckFile'

const ColorlibConnector = withStyles({
	alternativeLabel: {
		top: 22,
	},
	active: {
		'& $line': {
			backgroundImage:
				'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
		},
	},
	completed: {
		'& $line': {
			backgroundImage:
				'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)',
		},
	},
	line: {
		height: 3,
		border: 0,
		backgroundColor: '#eaeaf0',
		borderRadius: 1,
	},
})(StepConnector);

const useColorlibStepIconStyles = makeStyles({
	root: {
		backgroundColor: '#ccc',
		zIndex: 1,
		color: '#fff',
		width: 50,
		height: 50,
		display: 'flex',
		borderRadius: '50%',
		justifyContent: 'center',
		alignItems: 'center',
	},
	active: {
		backgroundImage:
			'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
		boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
	},
	completed: {
		backgroundImage:
			'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
	},
});

function ColorlibStepIcon(props) {
	const classes = useColorlibStepIconStyles();
	const { active, completed } = props;

	const icons = {
		1: <EmailIcon />,
		2: <CloudUploadIcon />,
		3: <PlaylistAddCheckIcon />,
	};

	return (
		<div
			className={clsx(classes.root, {
				[classes.active]: active,
				[classes.completed]: completed,
			})}
		>
			{icons[String(props.icon)]}
		</div>
	);
}

ColorlibStepIcon.propTypes = {
	active: PropTypes.bool,
	completed: PropTypes.bool,
	icon: PropTypes.node,
};

function TabPanel(props) {
	const { children, value, index, ...other } = props;

	return (
		<Typography
			component="div"
			role="tabpanel"
			hidden={value !== index}
			id={`full-width-tabpanel-${index}`}
			aria-labelledby={`full-width-tab-${index}`}
			{...other}
		>
			<Box p={3}>{children}</Box>
		</Typography>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.any.isRequired,
	value: PropTypes.any.isRequired,
};

class Upload extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			steps: ['1. Create Stamp', '2. Upload File', '3. Check Files'],
			activeStep: 0,
			stamp: '',
			valid: 0
		};
	}

	handleNext = () => {
		this.setState({ activeStep: this.state.activeStep + 1, valid: this.state.valid - 1 })
	};

	handleBack = () => {
		this.setState({ activeStep: this.state.activeStep - 1, valid: this.state.valid + 1 })
	};

	handleChangeStamp = (stamp) => {
		this.setState({ stamp: stamp, valid: this.state.valid + 1 })
	}

	handleUploadSuccess = () => {
		this.setState({ valid: this.state.valid + 1 })
	}

	handleCheckSuccess = () => {
		this.setState({ valid: this.state.valid + 1 })
	}


	render() {
		const { classes } = this.props;

		return (
			<React.Fragment>
				<CssBaseline />
				<Container maxWidth="lg">
					<main>
						{/* Main featured post */}
						<Paper className={classes.mainFeaturedPost}>
							<Stepper alternativeLabel activeStep={this.state.activeStep} connector={<ColorlibConnector />} className={classes.stepper}>
								{this.state.steps.map(label => (
									<Step key={label}>
										<StepLabel StepIconComponent={ColorlibStepIcon}>{label}</StepLabel>
									</Step>
								))}
							</Stepper>

							<SwipeableViews
								axis={this.props.theme.direction === 'rtl' ? 'x-reverse' : 'x'}
								index={this.state.activeStep}
							>
								<TabPanel value={this.state.activeStep} index={0} dir={this.props.theme.direction}>
									<Email onChangeStamp={this.handleChangeStamp} />
								</TabPanel>
								<TabPanel value={this.state.activeStep} index={1} dir={this.props.theme.direction}>
									<UploadFile stamp={this.state.stamp} onUploadDone={this.handleUploadSuccess} />
								</TabPanel>
								<TabPanel value={this.state.activeStep} index={2} dir={this.props.theme.direction}>
									<CheckFile stamp={this.state.stamp} onCheckDone={this.handleCheckSuccess} />
								</TabPanel>
							</SwipeableViews>

							<div style={{ textAlign: 'center' }}>
								{this.state.activeStep === this.state.steps.length ? (
									<div>
										<Typography className={classes.instructions}>
											All steps completed - you&apos;re finished
										</Typography>
									</div>
								) : (
										<div>
											<div>
												<Button disabled={this.state.activeStep === 0} onClick={this.handleBack} className={classes.button}>
													Back
													</Button>
												<Button
													variant="contained"
													color="primary"
													disabled={!(this.state.valid > 0)}
													onClick={this.handleNext}
													className={classes.button}
												>
													{this.state.activeStep === this.state.steps.length - 1 ? 'Finish' : 'Next'}
												</Button>
											</div>
										</div>
									)}
							</div>
						</Paper>

						<Grid container spacing={4} className={classes.Grid} />
					</main>
				</Container>
			</React.Fragment>
		);
	}
}

Upload.propTypes = {
	classes: PropTypes.object.isRequired
};

const styles = (theme) => ({
	mainFeaturedPost: {
		position: 'relative',
		backgroundColor: theme.palette.grey[50],
		marginBottom: theme.spacing(4),
		backgroundSize: 'cover',
		backgroundRepeat: 'no-repeat',
		backgroundPosition: 'center',
		padding: '1em'
		// border: '1px solid white',
	},
	Grid: {
		minHeight: '50vh'
	},
	margin: {
		margin: theme.spacing(1)
	},
	root: {
		width: '90%',
	},
	button: {
		marginRight: theme.spacing(1),
	},
	instructions: {
		marginTop: theme.spacing(1),
		marginBottom: theme.spacing(1),
	},
	stepper: {
		backgroundColor: theme.palette.grey[50],
	}
});

export default withStyles(styles, { withTheme: true })(Upload);
