import React from 'react';
import PropTypes from 'prop-types';
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'

import { withStyles } from '@material-ui/core/styles';
import { Button, CssBaseline, Grid, TextField, Typography, Container, FormControl, InputLabel, Select, MenuItem } from '@material-ui/core';

class Highchart extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      chartOptions: {
        chart: {
          type: 'scatter',
          zoomType: 'xy'
        },
        accessibility: {
          description: 'Correlation plot between two predicted results.'
        },
        title: {
          text: 'Correlation Plots'
        },
        subtitle: {
          text: 'Source: EpiClockR'
        },
        xAxis: {
          title: {
            enabled: true,
            text: 'Age (year)'
          },
          startOnTick: true,
          endOnTick: true,
          showLastLabel: true
        },
        yAxis: {
          title: {
            text: 'Age (year)'
          }
        },
        plotOptions: {
          scatter: {
            marker: {
              radius: 5,
              states: {
                hover: {
                  enabled: true,
                  lineColor: 'rgb(100,100,100)'
                }
              }
            },
            states: {
              hover: {
                marker: {
                  enabled: false
                }
              }
            },
            tooltip: {
              headerFormat: '<b>{series.name}</b><br>',
              pointFormat: '{point.x} cm, {point.y} kg'
            }
          }
        },
        series: [
          {
            name: 'data',
            color: '#0c2233',
            data: []
          }
        ]
      }
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.series !== this.props.series) {
      console.log(nextProps.series)
      this.setState({
        chartOptions: {
          series: [
            { data: nextProps.series }
          ]
        }
      });
    }
  }

  render() {
    const { classes } = this.props;
    const { chartOptions } = this.state;

    return (
      <HighchartsReact
        highcharts={Highcharts}
        options={chartOptions}
        oneToOne={true}
      />
    );
  }
}

Highchart.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
});

export default withStyles(styles)(Highchart);