import React from 'react';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';
import { CssBaseline, Container, Button, Paper } from '@material-ui/core'

import axios from 'axios';

class Trigger extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  handleTrigger = () => {
    axios.get(process.env.REACT_APP_API_ENTRY + '/trigger')
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      })
  }


  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth="lg">
          <main>
            <Paper className={classes.mainFeaturedPost}>
              <Button onClick={this.handleTrigger}> Anslysis </Button>
            </Paper>
          </main>
        </Container>
      </React.Fragment>
    );
  }
}

Trigger.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  mainFeaturedPost: {
    position: 'relative',
    backgroundColor: theme.palette.grey[50],
    marginBottom: theme.spacing(4),
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    padding: '1em'
    // border: '1px solid white',
  }
});

export default withStyles(styles)(Trigger)