import React from 'react';
import PropTypes from 'prop-types';
import compose from 'recompose/compose';
import { connect } from 'react-redux';
import { setSnack } from '../redux/actions'

import { withStyles } from '@material-ui/core/styles';
import { CssBaseline, Container, Paper, Grid, Chip, TextField, Button, Typography, Box, Divider, Checkbox, FormControlLabel, List, ListItem, ListItemText, ListItemSecondaryAction } from '@material-ui/core'
import DoneIcon from '@material-ui/icons/Done';

import axios from 'axios';

class Sample extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sampleResult: [],
      valid: '',
      dataload: false
    }
  }

  componentDidMount() {
    let query = new URLSearchParams(this.props.location.search);
    let valid = query.get('valid')
    if (valid !== null | valid !== '') {
      this.setState({ valid: valid })
      this.handleFetchSampleResult(valid)
    } else {
      this.props.setSnack('No param found', "error")
    }

  }

  handleFetchSampleResult = (valid) => {
    var self = this
    axios.get(process.env.REACT_APP_API_ENTRY + '/fetchsampleresult', {
      params: {
        valid: valid
      }
    })
      .then(function (response) {
        console.log(response.data.data.Result[0])
        self.setState({ sampleResult: response.data.data.Result[0], dataload: true })
      })
      .catch(function (error) {
        console.log(error);
      })
  }


  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth="lg">
          <main>
            <Paper className={classes.mainFeaturedPost}>
              <Typography variant="h5" component="h1" gutterBottom >
                <b>{this.state.valid.split("___").slice(-1)[0]}</b>
              </Typography>
              <Divider style={{ marginBottom: '10px' }} />
            </Paper>
            <Grid container className={classes.Grid}>
              {
                this.state.dataload ?
                  (
                    <Grid container spacing={2} >
                      <Grid item xs={4}>

                        <Paper className={classes.paper}>
                          <Typography variant="h6" gutterBottom>
                            <b>Stamp Info</b>
                          </Typography>
                          <List>
                            {['email', 'code', 'type', 'time', 'status'].map((label, index) => {
                              return (

                                <ListItem className={classes.ListItem}>
                                  <ListItemText
                                    classes={{ primary: classes.listItemText }}
                                    primary={label.toUpperCase()}
                                  />
                                  <ListItemSecondaryAction style={{ fontSize: '14px' }}>
                                    {
                                      label === 'time' ?
                                        (<> {new Date(this.state.sampleResult[label] * 1000).toISOString().slice(0, 19).replace('T', ' ')} </>) : (<>{this.state.sampleResult[label]}</>)
                                    }
                                  </ListItemSecondaryAction>
                                </ListItem>
                              )
                            })}
                          </List>
                        </Paper>
                      </Grid>
                      <Grid item xs={8}>
                        <Paper className={classes.paper}>
                          <Typography variant="h6">
                            <b>Predicted Result</b>
                          </Typography>
                          <Typography variant="body2" gutterBottom style={{ color: 'gray' }}>
                            ( For now, we don't save any middle result, only present final results you can see here.)
                    </Typography>
                          <br />
                          <Grid container spacing={3}>

                            {
                              Object.keys(this.state.sampleResult).slice(6).map((label, index) => {
                                return (
                                  <Grid item xs={6} md={4} lg={3} key={index}>
                                    <Paper className={classes.Modelpaper}>
                                      <Typography component="div">
                                        <Box fontFamily="Monospace" fontSize="subtitle1.fontSize" fontWeight="fontWeightLight" m={1}>
                                          {label.substring(2)}
                                        </Box>
                                        <Box fontFamily="Monospace" fontSize="h5.fontSize" fontWeight="fontWeightBold" m={1}>
                                          {this.state.sampleResult[label]}
                                        </Box>
                                      </Typography>
                                    </Paper>
                                  </Grid>

                                )
                              })
                            }
                          </Grid>
                        </Paper>
                      </Grid>
                    </Grid>
                  ) :
                  (
                    <Container component='main' maxWidth='xs'>
                      <Typography variant="body1" gutterBottom style={{ color: 'gray' }}>
                        ( Loading Data from Server... )
                    </Typography>
                    </Container>
                  )
              }
            </Grid>

          </main>
        </Container>
      </React.Fragment>
    );
  }
}

Sample.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  mainFeaturedPost: {
    position: 'relative',
    backgroundColor: theme.palette.grey[50],
    marginBottom: theme.spacing(4),
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    padding: '1em',
    // border: '1px solid white',
  },
  Grid: {
    minHeight: '40vh',
    marginBottom: theme.spacing(4)
  },
  Modelpaper: {
    margin: theme.spacing(1),
    padding: theme.spacing(1),
    textAlign: 'center',
    color: "white",
    backgroundColor: "#0c2233",
  },
  button: {
    margin: theme.spacing(1),
  },
  overFlowDiv: {
    overflow: 'auto',
    maxHeight: '40em'
  },
  wrapText: {
    wordWrap: 'break-word',
    wordBreak: 'break-all'
  },
  titleGrid: {
    padding: theme.spacing(1),
    marginBottom: "10px",
    fontWeight: "bold",
    color: 'white',
    backgroundColor: "#0c2233"
  },
  recordGrid: {
    padding: theme.spacing(1),
    '&:hover': {
      textShadow: '-0.06ex 0 #2E84CF, 0.06ex 0 #2E84CF',
      backgroundColor: '#f5f5f5'
    }
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  textfild: {
    color: '#2E84CF'
  },
  markdown: {
    ...theme.typography.body2,
    fontSize: 14,
  },
  ListItem: {
    borderBottom: '1px solid gray'
  },
  listItemText: {
    fontWeight: 'bold'
  },
  paper: {
    padding: theme.spacing(3)
  }
});

export default compose(
  withStyles(styles),
  connect(
    null,
    { setSnack }
  )
)(Sample);