import React from 'react';
import PropTypes from 'prop-types';
import compose from 'recompose/compose';
import { connect } from 'react-redux';
import { setSnack } from '../redux/actions'

import { withStyles } from '@material-ui/core/styles';
import { CssBaseline, Container, Paper, Grid, FormControl, InputLabel, Select, Typography, Box, Divider, Checkbox, FormControlLabel, List, ListItem, ListItemText, ListItemSecondaryAction } from '@material-ui/core'

import ReactVirtualizedTable from "../components/Summary/ReactVirtualizedTable"
import Highchart from "../components/Summary/Highchart"

import axios from 'axios';

class Summary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      summaryResult: [],
      record: [],
      email: '',
      code: '',
      time: '',
      dataload: false,
      Y: '',
      X: '',
      forHighchart: []
    }
  }

  componentDidMount() {
    let query = new URLSearchParams(this.props.location.search);
    let email = query.get('email')
    let code = query.get('code')
    let time = query.get('time')
    if (email !== null | email !== '' | code !== null | code !== '' | code !== null | code !== '') {
      this.setState({ email: email, code: code, time: time })
      this.handleFetchSummaryResult(email, code, time)
    } else {
      this.props.setSnack('No param found', "error")
    }

  }

  handleFetchSummaryResult = (email, code, time) => {
    var self = this
    axios.get(process.env.REACT_APP_API_ENTRY + '/fetchsummaryresult', {
      params: {
        email: email,
        code: code,
        time: time
      }
    })
      .then(function (response) {
        self.setState({ summaryResult: response.data.data.Result["result"], record: response.data.data.Result["record"][0], dataload: true })
      })
      .catch(function (error) {
        console.log(error);
      })
  }

  handleYChange = Yaxis => event => {
    this.setState({ Y: event.target.value, });
    if (event.target.value !== '' & this.state.X !== '') {
      this.CalculateForHighchart(this.state.X,event.target.value)
    }
  };

  handleXChange = Xaxis => event => {
    this.setState({ X: event.target.value, });
    if (this.state.Y !== '' & event.target.value !== '') {
      this.CalculateForHighchart(event.target.value,this.state.Y)
    }
  };

  CalculateForHighchart = (xname, yname) => {
    let tmp = []
    this.state.summaryResult.forEach((sample, index) => {
      tmp.push([sample[xname], sample[yname]])
    })
    this.setState({ forHighchart: tmp })
  }


  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth="lg">
          <main>
            <Paper className={classes.mainFeaturedPost}>
              <Typography variant="h5" component="h1" gutterBottom >
                <b> Summary </b>
              </Typography>
              <Typography variant="subtitle1" component="h1" gutterBottom >
                <b> {this.state.email} | {this.state.code} | {new Date(this.state.time * 1000).toISOString().slice(0, 19).replace('T', ' ')} </b>
              </Typography>
              <Divider style={{ marginBottom: '10px' }} />
            </Paper>
            <Grid container className={classes.Grid}>
              {
                this.state.dataload ?
                  (
                    <Grid container spacing={2} >
                      {/* <Grid item xs={4}>

                        <Paper className={classes.paper}>
                          <Typography variant="h6" gutterBottom>
                            <b>Summary Info</b>
                          </Typography>
                          <List>
                            {['email', 'code', 'type', 'time'].map((label, index) => {
                              return (

                                <ListItem className={classes.ListItem}>
                                  <ListItemText
                                    classes={{ primary: classes.listItemText }}
                                    primary={label.toUpperCase()}
                                  />
                                  <ListItemSecondaryAction style={{ fontSize: '14px' }}>
                                    {
                                      label === 'time' ?
                                        (<> {new Date(this.state.record[label] * 1000).toISOString().slice(0, 19).replace('T', ' ')} </>) : (<>{this.state.record[label]}</>)
                                    }
                                  </ListItemSecondaryAction>
                                </ListItem>
                              )
                            })}
                          </List>
                        </Paper>
                      </Grid> */}
                      <Grid item xs={12}>
                        <Paper className={classes.paper}>
                          <Typography variant="h6">
                            <b>Predicted Results</b>
                          </Typography>
                          <Typography variant="body2" gutterBottom style={{ color: 'gray' }}>
                            ( For now, we don't save any middle result, only present final results you can see here.)
                    </Typography>
                          <br />
                          <ReactVirtualizedTable results={this.state.summaryResult} />
                        </Paper>
                        <Paper className={classes.paper} style={{ marginTop: '2em' }}>
                          <Typography variant="h6">
                            <b>Plots</b>
                          </Typography>
                          <Grid container spacing={2} >
                            <Grid item xs={1} style={{ textAlign: 'center' }}>
                              <FormControl className={classes.formControl}>
                                <InputLabel htmlFor="age-native-simple">Y axis</InputLabel>
                                <Select
                                  native
                                  value={this.state.Y}
                                  onChange={this.handleYChange('Yaxis')}
                                  inputProps={{
                                    name: 'Yaxis',
                                    id: 'Yaxis',
                                  }}
                                >
                                  <option value="" />
                                  <option value={"myHorvath"}>Horvath</option>
                                  <option value={"myHannum"}>Hannum</option>
                                  <option value={"myEpiToc"}>EpiToc</option>
                                </Select>
                              </FormControl>
                            </Grid>
                            <Grid item xs={11} style={{ textAlign: 'center' }}>
                              <Highchart series={this.state.forHighchart} />

                              <FormControl className={classes.formControl}>
                                <InputLabel htmlFor="age-native-simple">Y axis</InputLabel>
                                <Select
                                  native
                                  value={this.state.X}
                                  onChange={this.handleXChange('Xaxis')}
                                  inputProps={{
                                    name: 'Xaxis',
                                    id: 'Xaxis',
                                  }}
                                >
                                  <option value="" />
                                  <option value={"myHorvath"}>Horvath</option>
                                  <option value={"myHannum"}>Hannum</option>
                                  <option value={"myEpiToc"}>EpiToc</option>
                                </Select>
                              </FormControl>
                            </Grid>
                          </Grid>

                        </Paper>
                      </Grid>
                    </Grid>
                  ) :
                  (
                    <Container component='main' maxWidth='xs'>
                      <Typography variant="body1" gutterBottom style={{ color: 'gray' }}>
                        ( Loading Data from Server... )
                    </Typography>
                    </Container>
                  )
              }
            </Grid>

          </main>
        </Container>
      </React.Fragment>
    );
  }
}

Summary.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  mainFeaturedPost: {
    position: 'relative',
    backgroundColor: theme.palette.grey[50],
    marginBottom: theme.spacing(4),
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    padding: '1em',
    // border: '1px solid white',
  },
  Grid: {
    minHeight: '40vh',
    marginBottom: theme.spacing(4)
  },
  Modelpaper: {
    margin: theme.spacing(1),
    padding: theme.spacing(1),
    textAlign: 'center',
    color: "white",
    backgroundColor: "#0c2233",
  },
  button: {
    margin: theme.spacing(1),
  },
  overFlowDiv: {
    overflow: 'auto',
    maxHeight: '40em'
  },
  wrapText: {
    wordWrap: 'break-word',
    wordBreak: 'break-all'
  },
  titleGrid: {
    padding: theme.spacing(1),
    marginBottom: "10px",
    fontWeight: "bold",
    color: 'white',
    backgroundColor: "#0c2233"
  },
  recordGrid: {
    padding: theme.spacing(1),
    '&:hover': {
      textShadow: '-0.06ex 0 #2E84CF, 0.06ex 0 #2E84CF',
      backgroundColor: '#f5f5f5'
    }
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  textfild: {
    color: '#2E84CF'
  },
  markdown: {
    ...theme.typography.body2,
    fontSize: 14,
  },
  ListItem: {
    borderBottom: '1px solid gray'
  },
  listItemText: {
    fontWeight: 'bold'
  },
  paper: {
    padding: theme.spacing(3)
  }
});

export default compose(
  withStyles(styles),
  connect(
    null,
    { setSnack }
  )
)(Summary);