import React from 'react';
import PropTypes from 'prop-types';
import compose from 'recompose/compose';
import { connect } from 'react-redux';
import axios from 'axios';

import { withStyles } from '@material-ui/core/styles';
import { Button, Paper, List, ListItem, ListItemText, ListItemSecondaryAction, Grid, Typography, Chip } from '@material-ui/core';

import DoneIcon from '@material-ui/icons/Done';
import ErrorIcon from '@material-ui/icons/Error';

import Markdown from 'markdown-to-jsx';
import Upload_CheckFile from '../../assets/markdown/Upload_CheckFile.md';

import { setSnack } from '../../redux/actions'

class CheckFile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      md: '',
      stamp: '',
      checkInfo: '',
      infoShow: false,
      lock: false
    };
  }

  componentWillMount() {
    fetch(Upload_CheckFile)
      .then((res) => res.text())
      .then((md) => {
        this.setState({ md })
      })
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.stamp !== this.props.stamp) {
      this.setState({
        stamp: nextProps.stamp
      });
    }
  }

  handleCheck = () => {
    if (this.state.stamp === '') {
      this.props.setSnack('Stamp missing', "error")
    } else {
      var self = this
      axios.get(process.env.REACT_APP_API_ENTRY + '/checkfile', {
        params: {
          stamp: this.state.stamp
        }
      })
        .then(function (response) {
          console.log(response);
          let res = response.data.data
          if (res.msg === 'error') {
            self.props.setSnack('Sorry, checking failed.', "error")
          } else {
            self.setState({ checkInfo: response.data.data, infoShow: true })
          }
        })
        .catch(function (error) {
          console.log(error);
        })
        .finally(function () {
          // always executed
        });
    }
  }

  handleConfirm = () => {
    if (this.state.checkInfo['valid'].length === 0) {
      this.props.setSnack('No Valid Sample', "error")
    } else {
      var self = this
      axios.get(process.env.REACT_APP_API_ENTRY + '/checkconfirm', {
        params: {
          email: this.state.checkInfo['email'],
          code: this.state.checkInfo['code'],
          type: this.state.checkInfo['type'],
          time: this.state.checkInfo['time'],
          valid: JSON.stringify(this.state.checkInfo['valid']),
          novalid: JSON.stringify(this.state.checkInfo['novalid'])
        }
      })
        .then(function (response) {
          console.log(response)
          let res = response.data.data
          if (res.msg === 'error') {
            self.props.setSnack('Sorry, File moving failed.', "error")
          } else {
            self.props.setSnack('Files ready for Analysis.', "success")
            self.setState({ lock: true })
            self.handleTrigger()
            self.props.onCheckDone()
          }
          // self.setState({ checkInfo: response.data.data, infoShow: true })
        })
        .catch(function (error) {
          console.log(error);
        })
        .finally(function () {
          // always executed
        });
    }
  }

  handleTrigger = () => {
    axios.get(process.env.REACT_APP_API_ENTRY + '/trigger')
      .then(function (response) {
        console.log(response);
        this.props.setSnack('Analysis has been Triggered.', "success")
      })
      .catch(function (error) {
        console.log(error);
      })
  }


  render() {
    const { classes } = this.props;

    return (

      <Grid container >
        <Grid item xs={12}>
          <Markdown className={classes.markdown} children={this.state.md} />
        </Grid>
        <Grid item xs={12}>
          <Grid container justify="center" className={classes.main}>
            <Button disabled={this.state.lock} variant="contained" color="primary" className={classes.button} onClick={this.handleCheck}>
              Run File Checking
            </Button>
          </Grid>
        </Grid>

        {
          this.state.infoShow ? (
            <Grid container spacing={2} >
              <Grid item xs={4}>

                <Paper className={classes.paper}>
                  <Typography variant="h6" gutterBottom>
                    <b>Stamp Info</b>
                  </Typography>
                  <List>
                    {['email', 'code', 'type', 'time'].map((label, index) => {
                      return (

                        <ListItem className={classes.ListItem}>
                          <ListItemText
                            classes={{ primary: classes.listItemText }}
                            primary={label.toUpperCase()}
                          />
                          <ListItemSecondaryAction style={{ fontSize: '14px' }}>
                            {
                              label === 'time' ?
                                (<> {new Date(this.state.checkInfo[label] * 1000).toISOString().slice(0, 19).replace('T', ' ')} </>) : (<>{this.state.checkInfo[label]}</>)
                            }
                          </ListItemSecondaryAction>
                        </ListItem>
                      )
                    })}
                  </List>
                </Paper>

                {/* <Paper className={classes.paperQueue}>
                  <Typography variant="h6" gutterBottom>
                    <b>Quene Info</b>
                  </Typography>
                  <span style={{ fontSize: '14px' }}>
                    Currently, there are <strong style={{ fontSize: '16px' }}>{this.state.checkInfo['currentQueneing']}</strong> samples to be processed before your data.
                  </span>
                </Paper> */}
              </Grid>
              <Grid item xs={8}>

                {
                  this.state.checkInfo['valid'].length > 0 ?
                    (
                      <Paper className={classes.paperSuccess}>
                        <Typography variant="h6">
                          <b>Valid Samples</b>
                        </Typography>
                        <Typography variant="body2" gutterBottom style={{ color: 'gray' }}>
                          ( To be preprocssed and run EpiClockR )
                    </Typography>
                        <br />
                        {
                          this.state.checkInfo['valid'].map((sample, index) => {
                            return (
                              <Chip
                                icon={<DoneIcon />}
                                key={index}
                                label={sample}
                                variant="outlined"
                                className={classes.chip}
                                // onDelete={handleDelete}
                                color="primary"
                              />
                            )
                          })
                        }
                      </Paper>
                    )
                    :
                    (
                      null
                    )
                }

                {
                  this.state.checkInfo['novalid'].length > 0 ?
                    (
                      <Paper className={classes.paperFail}>
                        <Typography variant="h6">
                          <b>Invalid Samples</b>
                        </Typography>
                        <Typography variant="body2" gutterBottom style={{ color: 'gray' }}>
                          ( To be removed from server )
                    </Typography>
                        <br />
                        {
                          this.state.checkInfo['novalid'].map((sample, index) => {
                            return (
                              <Chip
                                icon={<ErrorIcon />}
                                key={index}
                                label={sample}
                                variant="outlined"
                                className={classes.chip}
                                // onDelete={handleDelete}
                                color="secondary"
                              />
                            )
                          })
                        }
                      </Paper>
                    )
                    :
                    (
                      null
                    )
                }
              </Grid>
            </Grid>
          ) : null
        }

        {
          this.state.infoShow ? (
            <Grid item xs={12}>
              <Grid container justify="center" className={classes.main}>
                <Button disabled={this.state.lock} variant="contained" color="primary" className={classes.button} onClick={this.handleConfirm}>
                  Confirm and Submit
              </Button>
              </Grid>
            </Grid>
          ) : null
        }
      </Grid>
    );
  }
}

CheckFile.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  markdown: {
    ...theme.typography.body2,
    fontSize: 14,
  },
  main: {
    padding: theme.spacing(3),
  },
  ListItem: {
    borderBottom: '1px solid gray'
  },
  listItemText: {
    fontWeight: 'bold'
  },
  paper: {
    padding: theme.spacing(3)
  },
  paperSuccess: {
    padding: theme.spacing(3),
    // backgroundColor: '#f44336',
  },
  paperQueue: {
    padding: theme.spacing(3),
    marginTop: theme.spacing(2)
    // backgroundColor: '#f44336',
  },
  paperFail: {
    padding: theme.spacing(3),
    marginTop: theme.spacing(2),
    // backgroundColor: '#f44336',
    border: '3px solid #ff7043',
  },
  chip: {
    margin: theme.spacing(1),
    padding: theme.spacing(1),
    textShadow: 'none'
  }
});

export default compose(
  withStyles(styles),
  connect(
    null,
    { setSnack }
  )
)(CheckFile);