import React from 'react';
import PropTypes from 'prop-types';
import compose from 'recompose/compose';
import { connect } from 'react-redux';
import { setSnack } from '../redux/actions'
import { Link } from 'react-router-dom';

import { withStyles } from '@material-ui/core/styles';
import { CssBaseline, Container, Paper, Grid, TextField, Button, Typography, Box, Divider, Checkbox, FormControlLabel } from '@material-ui/core'

import Markdown from 'markdown-to-jsx';
import Result_Title from '../assets/markdown/Result_Title.md';

import axios from 'axios';

const groupBy = key => array =>
  array.reduce((objectsByKeyValue, obj) => {
    const value = obj[key];
    objectsByKeyValue[value] = (objectsByKeyValue[value] || []).concat(obj);
    return objectsByKeyValue;
  }, {});

const groupByBrand = groupBy('time');

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#2E84CF'
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: 'green'
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: 'lightgray'
      },
      '&:hover fieldset': {
        borderColor: 'black'
      },
      '&.Mui-focused fieldset': {
        borderColor: '#2E84CF'
      }
    }
  }
})(TextField);

class Result extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      code: '',
      md: '',
      myData: [],
      dataload: false
    }
  }

  componentWillMount() {
    fetch(Result_Title)
      .then((res) => res.text())
      .then((md) => {
        this.setState({ md })
      })
  }

  handleFetchResult = () => {
    if (this.state.email === '' | this.state.code === '') {
      this.props.setSnack('Email/Code missing.', "error")
    } else {
      var self = this
      axios.get(process.env.REACT_APP_API_ENTRY + '/fetchresult', {
        params: {
          email: this.state.email,
          code: this.state.code
        }
      })
        .then(function (response) {
          console.log(groupByBrand(response.data.data.Result));
          self.setState({ myData: groupByBrand(response.data.data.Result), dataload: true })
          self.props.setSnack('Data Fetched.', "success")
        })
        .catch(function (error) {
          console.log(error);
        })
    }
  }

  handleEmailChange = event => {
    this.setState({ email: event.target.value });
  };

  handleCodeChange = event => {
    this.setState({ code: event.target.value });
  };


  render() {
    const { classes } = this.props;

    return (
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth="lg">
          <main>
            <Paper className={classes.mainFeaturedPost}>

              <Grid container spacing={2}>
                <Grid item xs={12} md={6} style={{ paddingLeft: '3em' }}>
                  <Markdown className={classes.markdown} children={this.state.md} />
                </Grid>
                <Grid item xs={12} md={6}>
                  <Container component='main' maxWidth='xs'>
                    <Paper elevation={0} className={classes.paper}>
                      <CssTextField
                        className={classes.textfild}
                        value={this.state.email}
                        onChange={this.handleEmailChange}
                        variant='outlined'
                        margin='normal'
                        required
                        fullWidth
                        id='email'
                        label='Email'
                        name='email'
                        placeholder='champ450K@gmail.com'
                        autoFocus
                      />
                      <CssTextField
                        className={classes.textfild}
                        value={this.state.code}
                        onChange={this.handleCodeChange}
                        variant='outlined'
                        margin='normal'
                        required
                        fullWidth
                        name='code'
                        placeholder='Demo450K'
                        label='Data Code'
                        id='code'
                      />
                      <Button
                        type='submit'
                        fullWidth
                        variant='contained'
                        className={classes.submit}
                        style={{ backgroundColor: '#2E84CF', color: 'white' }}
                        onClick={this.handleFetchResult}
                      >
                        Fetch Result
                        </Button>
                    </Paper>
                  </Container>
                </Grid>
              </Grid>
            </Paper>


            {
              this.state.dataload ?
                (
                  Object.keys(this.state.myData).map((keyName, index) => {
                    return (
                      <Paper className={classes.mainFeaturedPost}>
                        <Container component='main' maxWidth='lg' key={index}>
                          <Paper elevation={0} className={classes.paper}>
                            <Typography variant="subtitle1" gutterBottom >
                              <b>Time Stamp: {new Date(keyName * 1000).toISOString().slice(0, 19).replace('T', ' ')}</b>

                              <Button variant="outlined" color="primary" className={classes.button}
                                style={{ float: 'right' }}
                                component={Link}
                                to={'/summary?email=' + this.state.email + '&code=' + this.state.code + '&time=' + keyName}>
                                Summary
                              </Button>
                            </Typography>
                            <Divider style={{ marginBottom: '20px' }} />
                            <div className={classes.overFlowDiv}>
                              <Grid container className={classes.titleGrid}>
                                <Grid item xs={3}>
                                  <Typography component="div">
                                    <Box fontFamily="fontFamily" m={1}>
                                      IDAT_NAME
                                      </Box>
                                  </Typography>
                                </Grid>
                                <Grid item xs={3}>
                                  <Typography component="div">
                                    <Box fontFamily="fontFamily" m={1}>
                                      IDAT_TYPE
                                      </Box>
                                  </Typography>
                                </Grid>
                                <Grid item xs={3}>
                                  <Typography component="div">
                                    <Box fontFamily="fontFamily" m={1}>
                                      STATUS
                                      </Box>
                                  </Typography>
                                </Grid>
                                <Grid item xs={3}>
                                  <Typography component="div">
                                    <Box fontFamily="fontFamily" m={1}>
                                      DETAIL
                                      </Box>
                                  </Typography>
                                </Grid>
                              </Grid>
                              {this.state.myData[keyName].map((record, m) => {
                                return (
                                  <Grid container key={m} className={classes.recordGrid}>
                                    <Grid item xs={3}>
                                      <Typography component="div" className={classes.wrapText}>
                                        <Box fontFamily="fontFamily" m={1}>
                                          {record['valid.name'].split("___").slice(-1)[0]}
                                        </Box>
                                      </Typography>

                                    </Grid>
                                    <Grid item xs={3}>
                                      <Typography component="div">
                                        <Box fontFamily="fontFamily" m={1}>
                                          {record.type}
                                        </Box>
                                      </Typography>

                                    </Grid>
                                    <Grid item xs={3}>
                                      <FormControlLabel
                                        control={
                                          <Checkbox checked={record.status === 'Done'} value="Done" color="primary" />
                                        }
                                        label={record.status}
                                      />
                                    </Grid>
                                    <Grid item xs={3}>

                                      <Button
                                        size="small"
                                        variant="outlined"
                                        color="primary"
                                        disabled={record.status !== 'Done'}
                                        className={classes.button}
                                        component={Link}
                                        to={'/sample?valid=' + record['valid.name']}>
                                        Check Detail
                                        </Button>
                                    </Grid>
                                  </Grid>
                                )
                              })}
                            </div>
                          </Paper>
                        </Container>
                      </Paper>
                    )
                  })
                ) : null
            }

          </main>
        </Container>
      </React.Fragment>
    );
  }
}

Result.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  mainFeaturedPost: {
    position: 'relative',
    backgroundColor: theme.palette.grey[50],
    marginBottom: theme.spacing(4),
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    padding: '1em',
    // border: '1px solid white',
  },
  Grid: {
  },
  paper: {
    marginBottom: theme.spacing(3),
    textAlign: 'left',
    backgroundColor: theme.palette.grey[50],
  },
  button: {
    margin: theme.spacing(1),
  },
  overFlowDiv: {
    overflow: 'auto',
    maxHeight: '40em'
  },
  wrapText: {
    wordWrap: 'break-word',
    wordBreak: 'break-all'
  },
  titleGrid: {
    padding: theme.spacing(1),
    marginBottom: "10px",
    fontWeight: "bold",
    color: 'white',
    backgroundColor: "#0c2233"
  },
  recordGrid: {
    padding: theme.spacing(1),
    '&:hover': {
      textShadow: '-0.06ex 0 #2E84CF, 0.06ex 0 #2E84CF',
      backgroundColor: '#f5f5f5'
    }
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  textfild: {
    color: '#2E84CF'
  },
  markdown: {
    ...theme.typography.body2,
    fontSize: 14,
  }
});

export default compose(
  withStyles(styles),
  connect(
    null,
    { setSnack }
  )
)(Result);