import React from 'react';
import PropTypes from 'prop-types';
import compose from 'recompose/compose';
import { connect } from 'react-redux';

import { withStyles } from '@material-ui/core/styles';
import { Button, CssBaseline, Grid, TextField, Typography, Container, FormControl, InputLabel, Select, MenuItem } from '@material-ui/core';

import Markdown from 'markdown-to-jsx';
import Upload_Email from '../../assets/markdown/Upload_Email.md';

import { setSnack } from '../../redux/actions'

class Email extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      code: '',
      type: '',
      stamp: '',
      md: ''
    };
  }

  componentWillMount() {
    fetch(Upload_Email)
      .then((res) => res.text())
      .then((md) => {
        this.setState({ md })
      })
  }

  handleSubmit = event => {
    event.preventDefault();

    if (this.state.email === '' | this.state.code === '' | this.state.type === '') {
      this.props.setSnack('Email/Code/Type missing.', "error")
    } else {
      var ts = Math.round((new Date()).getTime() / 1000);
      this.props.onChangeStamp(this.state.email + '___' + this.state.code + '___' + this.state.type + '___' + ts)
      this.setState({ stamp: this.state.email + '___' + this.state.code + '___' + this.state.type + '___' + ts })
    }
  };

  handleEmailChange = event => {
    this.setState({ email: event.target.value });
  };

  handleCodeChange = event => {
    this.setState({ code: event.target.value });
  };

  handleTypeChange = event => {
    this.setState({ type: event.target.value });
  };


  render() {
    const { classes } = this.props;

    return (

      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <Markdown className={classes.markdown} children={this.state.md} />
        </Grid>
        <Grid item xs={12} md={6}>
          <Container component='main' maxWidth='xs'>
            <CssBaseline />
            <div className={classes.paper}>

              <form
                className={classes.form}
                noValidate
                onSubmit={this.handleSubmit}>
                <CssTextField
                  className={classes.textfild}
                  value={this.state.email}
                  onChange={this.handleEmailChange}
                  variant='outlined'
                  margin='normal'
                  required
                  fullWidth
                  id='name'
                  label='email'
                  name='email'
                  placeholder='cham450K@gmail.com'
                  autoFocus
                />
                <CssTextField
                  className={classes.textfild}
                  value={this.state.code}
                  onChange={this.handleCodeChange}
                  variant='outlined'
                  margin='normal'
                  required
                  fullWidth
                  name='code'
                  placeholder='Demo450k'
                  label='Data Code'
                  id='code'
                />

                <FormControl className={classes.formControl}>
                  <InputLabel id="demo-simple-select-label">Data Type</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={this.state.type}
                    onChange={this.handleTypeChange}
                  >
                    <MenuItem value={'EPIC'}>EPIC</MenuItem>
                    <MenuItem value={'450K'}>450K</MenuItem>
                  </Select>
                </FormControl>

                <Button
                  type='submit'
                  fullWidth
                  variant='contained'
                  className={classes.submit}
                  style={{ backgroundColor: '#2E84CF', color: 'white' }}>
                  Submit
            </Button>
                {
                  this.state.stamp !== '' ?
                    (<div className={classes.stamp}>
                      <Typography variant="h6" gutterBottom>
                        <b>Stamp Generated</b>
      </Typography>
                      {this.state.stamp}
                    </div>) : null
                }

              </form>
            </div>
          </Container>
        </Grid>

      </Grid>
    );
  }
}

Email.propTypes = {
  classes: PropTypes.object.isRequired
};

const styles = theme => ({
  paper: {
    margin: theme.spacing(4),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    fontWeight: 'bold'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1)
  },
  submit: {
    margin: theme.spacing(3, 0, 2)
  },
  textfild: {
    color: '#2E84CF'
  },
  demolink: {
    '&:hover': {
      cursor: 'pointer',
      textShadow: '-0.06ex 0 grey, 0.06ex 0 grey',
      textDecoration: 'underline'
    }
  },
  markdown: {
    ...theme.typography.body2,
    fontSize: 14,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  stamp: {
    border: '2px solid #0c2233',
    margin: theme.spacing(4, 0),
    padding: '2em',
    borderRadius: '5px',
    fontWeight: 'light',
    fontSize: '15px',
    wordWrap: 'break-word',
    wordBreak: 'break-all',
    textAlign: 'center'
  }
});

const CssTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: '#2E84CF'
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: 'green'
    },
    '& .MuiOutlinedInput-root': {
      '& fieldset': {
        borderColor: 'lightgray'
      },
      '&:hover fieldset': {
        borderColor: 'black'
      },
      '&.Mui-focused fieldset': {
        borderColor: '#2E84CF'
      }
    }
  }
})(TextField);

export default compose(
  withStyles(styles),
  connect(
    null,
    { setSnack }
  )
)(Email);