import React from 'react';
import Header from '../components/Header';
import Footer from './Footer';

const HeaderStyle = {
  position: 'absolute',
  top: '0',
  left: '0',
  width: '100%',
  height: '20em',
  backgroundColor: '#0c2233',
  WebkitBoxShadow: '0px 1px 2px black',
  MozBoxShadow: '0px 1px 2px black',
  boxShadow: '0px 1px 2px black'
};

const LayoutStyle = {
  position: 'fixed',
  top: '0',
  left: '0',
  width: '100%',
  height: '100%',
  backgroundColor: '#eeeeee',
  overflow: 'auto'
};

const Layout = props => (
  <div style={LayoutStyle}>
    <div style={HeaderStyle}>
      <Header />
      {props.children}
      <Footer />
    </div>
  </div>
);

export default Layout;